import { AzureReportTypePaths, } from 'api/reports/azureReports';
import axios from 'axios';
export function runExport(reportType, query) {
    const path = AzureReportTypePaths[reportType];
    return axios.get(`${path}?${query}`, {
        headers: {
            Accept: 'text/csv',
        },
    });
}
